
import header from '@/components/header'
import dateSearch from '@/components/dateSearch'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import { MessageBox, Indicator, Toast } from 'mint-ui'
import API from '@/api/vacation'
import zcImg from '../../assets/attendance/zc.png'
import ycImg from '../../assets/attendance/yc.png'

export default {
components: {
Divider,
Spinner,
'v-header': header,
'v-date-search': dateSearch
},
data () {
return {
headtitle: '审批列表',
isActiveA: 'tab-btn-active',
isActiveB: '',
startDate: '',
endDate: '',
pageSize: 10,
isChecked: false,
pageNo: 1,
status: {
'01': {
class: 'check-none',
name: '未审核'
},
'02': {
class: 'pass',
name: '科室审核通过'
},
'03': {
class: 'pass',
name: '分管领导审核通过'
},
'04': {
class: 'pass',
name: '审核通过'
},
'05': {
class: 'checked',
name: '审核不通过'
}
},
vacationList: [],
bottomStatus: '',
bottom_tip: '', // 下拉提示语
zcImg: zcImg, // 正常图片
ycImg: ycImg, // 异常图片
allUseLoad: false,
from: '' 
}
},
beforeRouteEnter (to, from, next) {
let fromname = from.name
next(vm => {
vm.from = fromname
if (vm.from === 'checkTab') {
vm.queryName = ''
} else {
vm.queryName = 'leave'
}
})
},
mounted () {
this.getUncheckedData()
},
methods: {
applyVacation () {
if (this.$store.getters.getUser.data.dept === '5101080') {
this.$router.push({ name: 'applyVacationDept' })
} else {
this.$router.push({ name: 'applyVacationWindow' })
}
},
getUncheckedData () {
this.pageNo = 1
this.isActiveA = 'tab-btn-active'
this.isActiveB = ''
this.isChecked = false
this.vacationList = []
this.getData()
},
getCheckedData () {
this.pageNo = 1
this.isActiveA = ''
this.isActiveB = 'tab-btn-active'
this.isChecked = true
this.vacationList = []
this.getData()
},
search () {
this.vacationList = []
this.getData()
},
getData (callback) {
let token = this.$store.getters.getUser.token
Indicator.open({
text: '正在加载...',
spinnerType: 'fading-circle'
})
var param = {
token,
perpage: this.pageSize,
pageNo: this.pageNo,
start_time: this.startDate,
end_time: this.endDate



}
if (!this.isChecked) { // 未审核
param.validate = 'db'
param.zhgl_qj01018 = '01'
} else { // 已审核
param.validate = 'yb'
}
if (this.$store.getters.getMenuType === '02') { // 窗口审批
param.zhgl_qj01027 = '01'
} else if (this.$store.getters.getMenuType === '04') { // 审批局审批
param.zhgl_qj01027 = '02'
}
API.WINDOW_GET_CHECK_LIST(this, param).then((rep) => {
let result = rep.body
Indicator.close()
if (result.success) {

result.data.forEach((item, i) => {
if (this.$store.getters.getMenuType === '01' || this.$store.getters.getMenuType === '02') {
item.qjgs = item.zhgl_qj01030
} else if (this.$store.getters.getMenuType === '03' || this.$store.getters.getMenuType === '04') {
item.qjgs = item.zhgl_qj01008
}
})
this.vacationList = this.vacationList.concat(result.data)
if (result.data.length < this.pageSize) {
this.isHaveMore(false)
this.bottom_tip = '数据已全部加载完'
if (callback) {
callback()
}
} else {
this.bottom_tip = '上拉加载更多'
this.isHaveMore(true)
if (callback) {
callback()
}
}
} else {
MessageBox.alert(result.message, '操作提示')
}
}, (rep) => {
Indicator.close()
Toast({
message: '服务器异常',
position: 'middle',
duration: 1000
})
})
},
loadBottom: function () {

this.pageNo++
this.getData(this.$refs.loadmore.onBottomLoaded) 
},
loadmorechange (status) {
this.bottomStatus = status
if (status !== 'loading' && status === 'drop') {
this.bottom_tip = '释放加载'
} else if (status === 'loading') {
this.bottom_tip = '正在加载中'
}
},
isHaveMore: function (isHaveMore) {

this.allUseLoad = true
if (isHaveMore) {
this.allUseLoad = false
}
},
goToFlowPage (id) {
let isChecked = this.isChecked
this.$store.commit('setVacationId', { data: id })
this.$store.commit('setIsChecked', { data: isChecked })
if (this.$store.getters.getMenuType === '02') { // 窗口
this.$router.push({ name: 'vacationCheckWindow' })
} else if (this.$store.getters.getMenuType === '04') { // 审批局
this.$router.push({ name: 'vacationCheckDept' })
}
}
}
}




